.message-wrapper {
    text-align: start;
}

.message-wrapper.message-yours {
    text-align: end;
}

.message-wrapper.message-yours > .message {
    background-color: lightgreen;
}

.message {
    display: inline-block;
    max-width: 80%;
    background-color: lightgray;
    border-radius: 25px;
    padding: 10px;
    margin: 5px 0;
    text-align: start;
}
